@import url(https://fonts.googleapis.com/css?family=M+PLUS+Rounded+1c:300,400,700,900&display=swap);
body {
  margin: 0;
  font-family: 'M PLUS Rounded 1c', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

.Page {
  font-size: 15px;
  display: flex;
  flex-direction: row;
  height: 100%; }

.navbar {
  background-color: whitesmoke; }
  .navbar .blank {
    height: 25%; }

.active :nth-child(1) {
  background-color: white; }

.image-icon-container {
  padding: 1em; }
  .image-icon-container .image-icon {
    width: 2em;
    height: 2em;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat; }
  .image-icon-container:active {
    background-color: white; }

.image-icon-container.logo {
  background-color: #ffca3a; }

.image-profile-container .image-profile {
  background-image: url("/images/bnw.png");
  width: 32.5vw;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: left; }

.layout {
  display: flex;
  width: 100%;
  align-self: center;
  justify-content: center; }

.layout.row {
  flex-direction: row; }

.layout.column {
  flex-direction: column; }

.text::-moz-selection {
  background-color: #ffca3a;
  color: whitesmoke; }
.text::selection {
  background-color: #ffca3a;
  color: whitesmoke; }

.icon {
  font-size: 3em;
  color: whitesmoke;
  margin: .5em .25em; }
  .icon:hover {
    color: #ffca3a; }

.App {
  text-align: center;
  height: 100vh;
  background-color: #1d1d1d; }

